import React, {useEffect, useState} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {useMediaQuery} from "react-responsive";

import {closeBurgerMenu, openBurgerMenu} from "../../redux-toolkit/app/app-slice";
import {getIsBurgerMenuOpen, getScrollDown} from "../../redux-toolkit/app/app-selectors";

import Nav from "./nav";

import navCss from "./navCss.module.scss"
import navLinkCss from "./nav-link/navLinkCss.module.scss"

const NavContainer = () => {
    const [open, setOpen] = useState(false)
    const [classesForCross, setClassesForCross] = useState([navCss.cross]);
    const [classesForMenu, setClassesForMenu] = useState([navLinkCss.menu]);
    const [classesForNav, setClassesForNav] = useState([]);

    const isLess768 = useMediaQuery({query: `(max-width: 768px)`})

    const isScrollDown = useSelector(getScrollDown);
    const isBurgerMenuOpen = useSelector(getIsBurgerMenuOpen);

    const dispatch = useDispatch();

    //Сброс классов меню
    const resetClassNames = () => {
        setClassesForCross([navCss.cross]);
        setClassesForMenu([navLinkCss.menu]);
    }

    //Открытие меню nav
    const openMenu = () => {
        setClassesForCross([navCss.cross, navCss.active]);
        setClassesForMenu([navLinkCss.menu, navLinkCss.active]);
        setOpen(true);
        dispatch(openBurgerMenu());
    }

    //Закрытие меню nav
    const closeMenu = () => {
        resetClassNames();
        setOpen(false);
        dispatch(closeBurgerMenu());
    }

    //Открытие закрытие меню nav
    const handleClick = () => {
        open ? closeMenu() : openMenu();
    }

    //Открытие закрытие меню nav при выборе ссылки
    const handleClickLi = (event) => {
        (event.target.localName === "a") && closeMenu();
    }

    //Авто изменение nav
    useEffect(() => {
        if (isLess768) {
            closeMenu();
            resetClassNames();
        } else {
            setOpen(false);
            resetClassNames();
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [isLess768])

    //Спрятать nav
    const hideNav = () => {
        setClassesForNav([navCss.hidden]);
    }

    //Показать nav
    const visibleNav = () => {
        setClassesForNav([]);
    }

    //При прокрутке скрывается/показывается nav
    useEffect(() => {
        if (isScrollDown && !isBurgerMenuOpen) {
            hideNav()
        } else {
            visibleNav();
        }
    }, [isScrollDown, isBurgerMenuOpen])


    return <Nav classesForCross={classesForCross.join(" ")} classesForMenu={classesForMenu.join(" ")}
                classesForNav={classesForNav.join(" ")}
                handleClick={handleClick} handleClickLi={handleClickLi}/>
}

export default NavContainer;
