import {createSlice} from "@reduxjs/toolkit";

const initialState = {
    burgerMenuOpen: false,
    scrollDown: false,
    scrollTop: 0,
};

const appSlice = createSlice({
    name: "appSlice",
    initialState,
    reducers: {
        openBurgerMenu: (state) => {
            state.burgerMenuOpen = true;
        },
        closeBurgerMenu: (state) => {
            state.burgerMenuOpen = false;
        },
        setScrollTop: (state, action) => {
            state.scrollTop = action.payload;
        },
        setScrollUp: (state) => {
            state.scrollDown = false;
        },
        setScrollDown: (state) => {
            state.scrollDown = true;
        }
    }
})

const {actions, reducer} = appSlice;

export const {openBurgerMenu, closeBurgerMenu, openNav, closeNav, setScrollTop, setScrollUp, setScrollDown} = actions;

export default reducer;