import React from 'react';

import projectItemCss from "./projectItemCss.module.scss";

const ProjectItem = ({srcBCG, altBCG, src, alt, nameProject, typeProject, descriptionProject}) => {
    return <div className={projectItemCss.projectsItem}>
        <div className={projectItemCss.projectsItemBcg}>
            <img src={srcBCG} alt={`${altBCG}`}/>
            <div className={projectItemCss.projectsItemProject}>
                <img src={src} alt={`${alt}`}/>
            </div>
        </div>
        <div className={projectItemCss.projectsItemText}>
            <p><span>{nameProject}</span> {typeProject}<br/>{descriptionProject}</p>
        </div>
    </div>
}

export default ProjectItem;
