import React from 'react';

import skillListItemCss from "./skillListItemCss.module.scss"

const SkillListItem = ({src, alt, nameSkill}) => {
    return <li className={skillListItemCss.skillsItem}>
        <div className={skillListItemCss.skillsIMG}>
            <img src={src} alt={`${alt}`}/></div>
        <div className={skillListItemCss.skillsText}>
            <p>{nameSkill}</p>
        </div>
    </li>
}

export default SkillListItem;
