import React from 'react';
import {useSelector} from "react-redux";

import Nav from "../nav/nav-container";
import Footer from "../footer/footer";

import navContentFooterLayoutCss from "./nav-content-footerLayoutCss.module.scss";

import {getIsBurgerMenuOpen} from "../../redux-toolkit/app/app-selectors";

const NavContentFooterLayout = ({children}) => {
    const isBurgerMenuOpen = useSelector(getIsBurgerMenuOpen);

    return <>
        <Nav/>
        <div className={isBurgerMenuOpen ? navContentFooterLayoutCss.blur : undefined}>
            {children}
            <Footer/>
        </div>
    </>
}

export default NavContentFooterLayout;
