import {useDispatch, useSelector} from "react-redux";
import {useEffect} from "react";
import {setScrollDown, setScrollTop, setScrollUp} from "./redux-toolkit/app/app-slice";
import {getScrollTop} from "./redux-toolkit/app/app-selectors";

import App from "./app";

function AppContainer() {
    const lastScrollTop = useSelector(getScrollTop);

    const dispatch = useDispatch();

    //Получаем инфу: вверх/вниз
    const handleScroll = () => {
        const curScrollTop = window.scrollY;

        if (curScrollTop > lastScrollTop) {
            dispatch(setScrollDown());
        } else {
            dispatch(setScrollUp());
        }

        dispatch(setScrollTop(curScrollTop));
    };

    useEffect(() => {
        window.addEventListener("scroll", handleScroll);
        return () => {
            window.removeEventListener("scroll", handleScroll);
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [lastScrollTop]);

    return <App/>;
}

export default AppContainer;
